import AppBar from '@material-ui/core/AppBar';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';

class Experience extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      strapiUrl: this.props.strapiUrl,
      classes: this.props.classes,
    };
  }

  getHeader = (xp) => {
    const { classes, strapiUrl } = this.state;

    return <CardHeader
      avatar={
        xp.Logo ? <img src={strapiUrl + xp.Logo.url} alt={xp.Entreprise} className={classes.xpimg} /> : null
      }
      className={classes.cardheader}
      title={xp.Entreprise}
    />
  };

  render() {
    const { classes, hp, experiences } = this.props;

    return (
      <div id="section-experience">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre exp√©riences']}
        </AppBar>
        {experiences.map(xp => (
          <Card key={xp._id} className={classes.card}>
            {this.getHeader(xp)}
            <CardContent>
              content
            </CardContent>
          </Card>
        ))}
      </div>
    );
  }
}

export default Experience;
